'use strict';

/**
* Parse a string as JSON.
*
* @module @stdlib/utils-parse-json
*
* @example
* var parseJSON = require( '@stdlib/utils-parse-json' );
*
* var obj = parseJSON( '{"beep":"boop"}' );
* // returns {'beep':'boop'}
*/

// MODULES //

var parseJSON = require( './parse_json.js' );


// EXPORTS //

module.exports = parseJSON;
